import React from 'react';
import { Slot } from 'expo-router';
import { useAuthGuard } from '@navigation/guards';
import { PatientFrame } from '@platform/layouts';

/**
 * Patient Group Layout
 *
 * Layout wrapper for patient-facing routes.
 * Per app-router.mdc: layouts use `_layout.jsx` and render `<Slot />`.
 */
const PatientLayout = () => {
  useAuthGuard();

  return (
    <PatientFrame testID="patient-route-layout">
      <Slot />
    </PatientFrame>
  );
};

export default PatientLayout;
